import React, { FC } from 'react'
import clsx from 'clsx'
import { PlusCircleIcon } from '../../icons/PlusCircle'
import { CartItemNext } from '../CartItemNext'
import { CartItemBundleProps } from './types'
import './CartItemBundle.scss'

const CartItemBundle: FC<CartItemBundleProps> = ({
  additionalItems = [],
  className,
  onBlur,
  onDecrease,
  onDelete,
  onIncrease,
  isCheckout = false,
  isLoading,
  isDisabled = false,
  ...parentItem
}) => {
  return (
    <div
      className={clsx(
        'CartItemBundle',
        isDisabled && '--isDisabled',
        className,
      )}
    >
      <CartItemNext
        className='CartItemBundle__ParentItem'
        isCheckout={isCheckout}
        isDisabled={isDisabled}
        isLoading={isLoading}
        onBlur={onBlur}
        onDecrease={onDecrease}
        onDelete={onDelete}
        onIncrease={onIncrease}
        {...parentItem}
      />
      {!isLoading &&
        additionalItems?.map((item, itemIdx) => (
          <div
            key={`${parentItem.title}-${item.title}-${itemIdx}`}
            className='CartItemBundle__Item'
          >
            <div className='CartItemBundle__ItemDivider'>
              <PlusCircleIcon color='var(--ui-green)' />
              <hr className='CartItemBundle__DividerLine' />
            </div>
            <CartItemNext
              className='CartItemBundle__ItemCard'
              isCheckout={isCheckout}
              isCountable={false}
              isDeletable={false}
              onBlur={onBlur}
              onDecrease={onDecrease}
              onDelete={onDelete}
              onIncrease={onIncrease}
              {...item}
            />
          </div>
        ))}
    </div>
  )
}

export default CartItemBundle
